.BColorButton,
.BColorButtonContainer {
  height: 30px;
  width: 30px;
  border-radius: 50%;
  outline: 0px;
  border: none;
  cursor: pointer;
}
.BColorButton:hover {
  cursor: pointer;
}
.BColorButtonContainer {
  width: 32px;
  height: 32px;
  background-image: linear-gradient(45deg, #ddd 25%, transparent 0), linear-gradient(-45deg, #ddd 25%, transparent 0), linear-gradient(45deg, transparent 75%, #ddd 0),
    linear-gradient(-45deg, transparent 75%, #ddd 0);
  background-size: 10px 10px;
  background-position: 0 0, 0 5px, 5px -5px, -5px 0;
  border: 1px solid #e9e9e9;
  box-sizing: border-box;
}
.bColorReset .dashicons-image-rotate {
  text-align: center;
}

.components-panel__row.width-100 div {
  width: 100%;
}
.components-panel__row.width-100 input {
  width: 240px;
  max-width: 100%;
}
.bMediaUpload .components-panel__row.width-100 input {
  // width: calc(100% - 39px);
  max-width: 100%;
  padding: 5px 8px;
}

.h5vpUrlInput h3 {
  margin: 0 10px;
  text-transform: uppercase;
  display: inline;
}
.h5vpUrlInput input {
  width: 300px;
  padding: 8.5px 15px;
  border-bottom-right-radius: 0;
  border-top-right-radius: 0;
  line-height: 0;
}
.h5vpUrlInput {
  margin: -4px;
}
.h5vpUrlInput button {
  margin-left: 20px;
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}
.bMediaUpload {
  position: relative;
}
.bMediaUpload button {
  position: absolute;
  right: 0px;
  height: 15px;
  padding: 3px !important;
  color: #fff !important;
  outline: 0;
}
.bMediaUpload button:focus {
  outline: 0;
  box-shadow: 0 0 0 0px #007cba !important;
}
.subtitleItem button {
  margin-top: -10px;
  padding: 0;
}
.pdfp [role="tablist"] {
  justify-content: space-around;
}
.iconButton {
  background: #ffffff;
  border: 1px solid #dddddd;
  display: inline-flex;
  align-items: center;
  padding: 0 !important;
  margin-left: 3px;
  height: 22px;
  width: 26px;
  cursor: pointer;
}
.iconButton span {
  font-size: 14px;
  width: auto;
  height: auto;
  margin: 0 auto;
}
.iconButton svg {
  width: auto;
  height: auto;
  margin: 0 auto;
}
.iconButton span:before {
  font-size: inherit;
}
button[bisactive="true"] {
  background: #061b9c;
  color: #ffffff;
  fill: #ffffff;
  border-radius: 3px;
}
button[bisactive="true"] svg {
  fill: #ffffff;
}
